    text-align: center;
    margin-top: 2%;
    line-height: 15px;
  }
  
  .fixed-menu-link {
    height: 40px;
    width: 50px;
    border-radius: 7px;
    color: #fff;
    flex-direction: column;
    box-sizing: border-box;
    display: flex;
    place-content: center;
    margin-right: 12px;
    cursor: pointer;
    font-size: 14px;
    margin-left: 5px;
  }
