.card-wrapper {
  /* border: 1px solid black; */
  position: relative;
  width: 100%;
  padding: 0 10px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.hotelInnerDiscoverDiv {
  /* border: 1px solid red !important; */
  width: 100%;
  border: none;
  border-radius: 16px !important;
  /* border-bottom-left-radius: 50%; */

  overflow: hidden;
  cursor: pointer;
  position: relative;
}

.hotelInnerDiscoverCardimage {
  top: 0;
  left: 0;
  width: 100%;
  height: 360px !important;
  z-index: 2;
  background-color: #000;
  -webkit-transition: 0.5s;
  transition: 0.5s;
  /* border-bottom-left-radius: 50%; */
}

.hotelInnerDiscoverCardimage img {
  width: 100%;
  height: 100%;
  filter: brightness(0.6);
  /* border-radius: 50%; */
  -o-object-fit: cover;
  object-fit: cover;
}

/* .hotelInnerDiscoverDiv img{
      border-bottom-left-radius: 25%;
    } */

.hotelInnerDiscoverDiv:hover img {
  -webkit-transition: 0.5s;
  transition: 0.5s;
}

.hotelInnerDiscoverDiv:hover .hotelInnerDiscoverCardimage {
  -moz-transform: scale(1.5);
  -webkit-transform: scale(1.5);
  transform: scale(1.5);
}

.hotelInnerDiscoverDetails {
  position: absolute;
  bottom: -45%;
  left: 0;
  width: 100%;
  height: 310px;
  z-index: 100000000;
  padding: 10px;
  -webkit-transition: 0.6s;
  transition: 0.6s;
  color: white;
}

