  flex-direction: row;
}

.contentButtoninnerdiv button{
  margin-left: 32%;
  border: none;
  width: 30%;
  color: white;
  font-size: 15px;
  background-color: #3380f2;
  border-radius: 5px;
}
.exclusivecarddesc:hover {
  color: white;
}

.newviewallActivityAroundtext{
  margin-top: 0.5%;
  border: 2px solid white;
  border-radius: 20px;
  width: 34%;
  height: 35px;
  font-size: 14px;
  font-weight: 700;
  padding-left: 16px;
  padding-top: 4px;
  margin-bottom: 8%;
  text-decoration: none !important;
}

.newviewallActivityAroundtext:hover{
  background-color: white;
  color: #3380f2;
}

.newActivityAroundHeadingbuttons {
  justify-content: flex-end;
  /* width: -1%; */
  margin-left: 6%;
  margin-top: -1%;
  background-color: #3380f2 !important;
}


.newActivityAroundHeadingbuttons button{
  color: #3380f2 !important;
  background-color: white !important;
}


.line-indicator li {
  height: 4px;
  width: 70px;
  background: lightgray;
  border-radius: 5px;
}

.line-indicator li:hover {
  background: gray;
}

.line-indicator li.slick-active {
  background: blue;
}

.buttons {
  background: rgb(51, 128, 242) !important;
  color: white !important;
  transition: 700ms;
}
.buttons:hover {
  transform: scale(1.1);
}
.newBudgetFriendlyButton {
  border: 3px solid #3380f2;
  width: 34%;
  border-radius: 15px !important;
  background-color: white;
  color: #3380f2;
  padding: 4px 9px;
  font-weight: 500;
  margin-bottom: 1%;
  margin-top: 3%;
  font-size: 15px !important;
}
.newBudgetFriendlyButton:hover {
  border: 3px solid white;
  background-color: #3380f2;
  color: white;
 
  font-size: 14px !important;
}


@media screen and (max-width: 950px) {
  .newBudgetFriendlyImagetextcontainer {
    flex-direction: column;
  }
  .col-md-4 {
    width: 90%;
    height: 280px;
    margin-left: 3%;
    margin-top: 10%;
  }
  .card-title {
    font-size: 17px;
  }
  .card-text {
    font-size: 13px;
  }
  .newviewallActivityAroundbuttons {
    display: none;
  }
  .newBudgetFriendlyHeading1 {
    display: none;
  }
  .newBudgetFriendlyHeadingcontainer {
    flex-direction: column;
  }
  .internationalheading2 {
    width: 100%;
  }
  .newBudgetFriendlyImagetextcontainer img {
    width: 100%;
    height: 100%;
  }
  .exclusivedealsimage {
    width: 100%;
    height: 100%;
  }
  .newBudgetFriendlyTextcard {
    margin-left: 8px;
  }
}

@media screen and (max-width: 450px) {
  .newBudgetFriendlyImagetextcontainer {
    flex-direction: column;
  }
  .col-md-4 {
    width: 90%;
    height: 280px;
    margin-left: 3%;
    margin-top: 5%;
  }
  .card-title {
    font-size: 17px;
  }
  .card-text {
    font-size: 13px;
  }
  .newviewallActivityAroundbuttons {
    display: inline-block;
    vertical-align: top;
    margin-top: -50px;
    text-align: right;
    width: 40%;
    font-size: 14px;
    margin-left: 52%;
  }
  .internationalheading2 {
    display: none;
  }
  .newActivityAroundHeadingbuttons {
    display: none;
  }
  .newBudgetFriendlyHeading1 {
    display: inline-block;
    vertical-align: top;
    width: 40%;
    font-size: 16px;
    margin-left: 3%;
  }
  .newBudgetFriendlyHeadingcontainer {
    flex-direction: column;
  }
  .newBudgetFriendlyImagetextcontainer img {
    width: 100%;
    height: 100%;
  }
  .exclusivedealsimage {
    width: 100%;
    height: 100%;
  }
  .newBudgetFriendlyTextcard {
    margin-left: 8px;
  }
}
