  .newDomesticNewAndHotCardwrapper {
    position: relative;
    width: 106%;
    padding: 0 10px;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
  }
  
  .newDomesticNewAndHotMaindiv {
      margin-left: 3%;
    width: 102%;
    border-radius: 20px !important;
    overflow: hidden;
    cursor: pointer;
    position: relative;
  }
  
  .newDomesticNewAndHotCardimage {
    top: 0;
    left: 0;
    width: 100%;
    height: 360px !important;
    border-radius: 20px !important;
    z-index: 2;
    background-color: #000;
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  
  .newDomesticNewAndHotCardimage img {
    width: 100%;
    filter: brightness(0.8);
    -o-object-fit: cover;
    object-fit: cover;
  }
  
  .newDomesticNewAndHotMaindiv:hover img {
    -webkit-transition: 0.5s;
    transition: 0.5s;
  }
  
  .newDomesticNewAndHotMaindiv:hover .newDomesticNewAndHotCardimage {
    -moz-transform: scale(1.5);
    -webkit-transform: scale(1.5);
    transform: scale(1.5);
  }
  
  .newDomesticNewAndHotStorydetails {
    position: absolute;
    bottom: -38%;
    left: 0;
    width: 100%;
    height: 310px;
    z-index: 100000000;
    padding: 10px;
    -webkit-transition: 0.6s;
    transition: 0.6s;
    line-height: 15px;
  }
  
  .newDomesticNewAndHotStorydetails h2 {
    margin: 0px 0;
    padding: 0;
    text-align: center;
    color: white;
    font-size: 25px;
    font-weight: 700;
  }
  
  .newDomesticNewAndHotStorydetails h2 .newDomesticNewAndHotjob_title {
    font-size: 14px;
    line-height: 1.5;
    color: white;
    font-weight: 300;
    display: block;
  }
  /* 
  .card:hover .newDomesticNewAndHotStorydetails {
    bottom: 0;
  } */
  
  .newDomesticNewAndHotSlideroutercontainer .slick-dots {
    bottom: -2.5vw;
  }
  
  .newDomesticNewAndHotSlideroutercontainer .slick-dots li button {
    width: 20px;
    height: 20px;
    border: 1px solid #fff;
    border-radius: 20px;
    background-color: #3380f2;
  }
  
  .newDomesticNewAndHotSlideroutercontainer .slick-dots li button:before {
    /* border: 1px solid red !important; */
    position: absolute;
    font-size: 20px;
    top: 50%;
    left: 50%;
    width: 10px;
    height: 10px;
    border-radius: 210px;
    content: "";
    text-align: center;
    opacity: 0.5;
    color: #000;
    background-color: white !important;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    -webkit-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out;
  }
  
  .carousel .slick-prev,
  .carousel .slick-next {
    border: 2px solid #3380f2;
    width: 40px;
    height: 50px;
    /* border-radius: 30px; */
    border-bottom-left-radius: 10px;
    border-top-left-radius: 10px;
    border-bottom-right-radius: 10px;
    border-top-right-radius: 10px;
    background-color: transparent !important;
  }
  
  .carousel .slick-prev:hover,
  .carousel .slick-prev:focus,
  .carousel .slick-next:hover,
  .carousel .slick-next:focus {
    color: #fff;
    outline: none;
    background: #fff;
  }
  
  .carousel .slick-prev:before,
  .carousel .slick-next:before {
    color: #000;
    font-family: "Raleway", sans-serif;
  }
  
  .carousel .slick-next {
    left: 100% !important;
  }
  
  .carousel .slick-prev {
    left: -55px;
  }
  
  
