.exclusivemaindiv {
  width: 99.3%;
  height: 550px;
  margin-bottom: 4%;
  /* border: 2px solid green; */
}
.exclusivedealsheadingcontainer {
  display: flex;
  flex-direction: row;
  width: 100%;
  color: #393939;
  margin: auto;
  background-color: white !important;
  margin-top: 1%;
  height: 80px;
}
.exclusivedealsheading1 {
  font-size: 35px;
  font-weight: 700;
  width: 20%;
  margin-left: 5%;
  margin-top: 1.4%;
  color: #3380f2;
}
.exclusivedealsheading2 {
  font-size: 15px;
  font-weight: 700;
  margin: auto;
  color: #393939;
  display: flex;
  flex-direction: row;
}
.exclusivedealsheading2 h3 {
  margin-left: 10%;
  font-size: 20px;
  margin-top: 2%;
  font-weight: 700;
}
.exclusivedealsheading2 h3:hover {
  text-decoration: underline;
}
.exclusivedealsheadingbuttons {
  /* border: 1px solid yellow !important; */
  color: #3380f2;
  justify-content: flex-end;
  width: -1%;
  margin-left: 100%;
  cursor: pointer;
  margin-top: 1%;
}

.exclusivedealsheadingbuttons button {
  color: #3380f2 !important;
  background-color: #3380f2 !important;
}
.exclusivedealsconatinerdiv {
  width: 100%;
  height: 450px;
  margin: auto;
  /* border: 2px solid gold; */
